// React  (useState, useEffect) 
import React, { useState, useEffect } from 'react';

// Firebase   Firestore  
import { db } from "../js/firebaseConfig";

// Firestore  (collection, addDoc, getDocs) 
import { collection, addDoc, getDocs } from 'firebase/firestore';

// UsersForm  
function UsersForm() {
  // [1]   
  // name:    
  const [name, setName] = useState('');
  // age:    
  const [age, setAge] = useState('');
  const [addr, setAddr] = useState('');
  // users: Firestore     
  const [users, setUsers] = useState([]);

  // [2]   
  // Firestore     
  const addUser = async () => {
    //    
    if (name && age) {
      // Firestore 'users'    
      await addDoc(collection(db, 'users'), { name, age: Number(age), addr });
      // addDoc  Firestore     .
      // collection(db, 'users') 'users'   .
      // { name, age: Number(age) }   .
      //  age   .

      //   
      setName('');
      setAge('');
      setAddr('');

      //   
      fetchUsers();
      // Firestore     ,
      //       .
      // , fetchUsers()  Firestore    .
    }
  };

  // [3] Firestore   
  // Firestore 'users'     
  const fetchUsers = async () => {
    // Firestore 'users'    
    const querySnapshot = await getDocs(collection(db, 'users'));
    // getDocs  Firestore     .
    // querySnapshot     .

    //     (id  )
    const usersList = querySnapshot.docs.map((doc) => {
      console.log(doc.id, " => ", doc.data()); //  ID  
      return ({ id: doc.id, ...doc.data() })
    });
    // map  querySnapshot.docs       
    //   .
    //    doc.id doc.data()   ID  .

    //    
    setUsers(usersList);
    // setUsers   , 
    // usersList    .
    // , usersList Firestore    .
  };

  // [4]       
  useEffect(() => {
    fetchUsers(); // Firestore   
  }, []); //        

  // [5]  
  return (
    <div>
      {/*  */}
      <h2>Firebase Users</h2>
      
      {/*    */}
      <input
        type="text" //  : 
        placeholder="Name" //     
        value={name} //   name 
        onChange={(e) => setName(e.target.value)} //     
      />
      
      {/*    */}
      <input
        type="number" //  : 
        placeholder="Age" //     
        value={age} //   age 
        onChange={(e) => setAge(e.target.value)} //     
      />

      {/*    */}
      <input
        type="test" //  : 
        placeholder="Address" //     
        value={addr} //   age 
        onChange={(e) => setAddr(e.target.value)} //     
      />
      
      {/*    */}
      <button onClick={addUser}>Add User</button>
      
      {/*    */}
      <ul>
        {users.map((user) => (
          //     
          <li key={user.id}>
            {user.name} ({user.age} years old) : {user.addr}
          </li>
        ))}
      </ul>
    </div>
  );
}

// UsersForm  
export default UsersForm;